export const FC_GetStringValue = (value: string | Date | null): string => {
  if (value === null) return "";
  if (FC_isDateValid(value)) return DATE(value);
  return value as string;
};

export const FC_GetHierarchStrings = (
  teacher_hierarch:
    | {
        grade_group: string;
        combination_code: string;
        combination_name: string;
        grade_name: string;
        grade_code: string;
        course_code: string;
        course_name: string;
        teaching_period: string;
      }[]
    | null
): {
  courses: string;
  grades: string;
  periods: string;
} => {
  let courses: string = "",
    course_code: string[] = [],
    grades: string = "",
    grade_obj: {
      grade_names: string[];
      grade_codes: string[];
      courses: string[];
      group: string[];
    } = {
      grade_names: [],
      grade_codes: [],
      courses: [],
      group: [],
    },
    periods: number = 0;

  if (teacher_hierarch) {
    for (const key in teacher_hierarch) {
      const element = teacher_hierarch[key];
      // CREATING DISTINCT COURSES ===========
      if (!course_code.includes(element.course_code)) {
        course_code.push(element.course_code);
        courses += isEmptyOrSpaces(courses)
          ? element.course_name
          : ", " + element.course_name;
      }

      // CREATING OBJECT OF DISTINCT GRADES WITH LENGTH GROUPED BY COURSE ===========
      if (!grade_obj.grade_codes.includes(element.grade_code)) {
        grade_obj.grade_codes.push(element.grade_code);
        grade_obj.grade_names.push(element.grade_name);
        grade_obj.courses.push(element.course_code);
        grade_obj.group.push(
          FC_ConvertArrayToString(JSON.parse(element.grade_group))
        );
        // grades += isEmptyOrSpaces(grades)
        //   ? element.course_name
        //   : ", " + element.course_name;
      } else if (
        grade_obj.courses.findIndex((item) => item === element.course_code) >= 0
      ) {
        let index = grade_obj.courses.findIndex(
          (item) => item === element.course_code
        );
        grade_obj.group[index] += JSON.parse(element.grade_group).length;
      }

      // GET TOTAL PERIODS MULTIPLIED WITH NUMBER OF GRADE GROUP ===========
      periods =
        periods +
        Number(element.teaching_period) *
          JSON.parse(element.grade_group).length;
    }

    // CREATING DISTINCT GRADES ===========
    for (const key in grade_obj.courses) {
      grades += isEmptyOrSpaces(grades)
        ? `${grade_obj.grade_names[key]} (${grade_obj.group[key]})`
        : `, ${grade_obj.grade_names[key]} (${grade_obj.group[key]})`;
    }
  }

  return { courses, grades, periods: periods.toString() };
};

export const FC_isDateValid = (value: string | Date | null): boolean => {
  if (value === null) return false;
  var dateWrapper = new Date(value);
  return !isNaN(dateWrapper.getDate());
};
