import axios from "axios";
import { APP_URL } from "../config/app.config";
import { ProfileType } from "../types/userInfo";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";

/**
 * * ****************************** ACTIONS *****************************
 */
export const FC_GetProfile = async (
  user_id: string,
  callBack: (status: boolean, data: ProfileType | null, msg: string) => void
) => {
  callBack(true, null, "");
  try {
    setAxiosToken();
    const res = await axios.get<ProfileType>(
      `${APP_URL}/profile/teacher/${user_id}`
    );
    console.log("=================FC_GetProfilesssss===================");
    console.log({ FC_GetProfile: res });
    console.log("=================FC_GetProfilesssss===================");

    // * return data
    callBack(false, res.data, "");
  } catch (error) {
    console.log(error);
    callBack(false, null, errorToText(error));
  }
};

export const FC_ResetPassword = async (
  user: { user_id: string; username: string; password: string },
  callBack: (status: boolean, msg: string) => void
) => {
  callBack(true, "");
  try {
    setAxiosToken();
    const res = await axios.post<any>(
      `${APP_URL}/user/updateinfo/credential/${user.user_id}`,
      {
        username: user.username,
        password: user.password,
      }
    );
    console.log("=================FC_ResetPassword===================");
    console.log({ FC_ResetPassword: res });
    console.log("=================FC_ResetPassword===================");

    // * return data
    callBack(false, "");
  } catch (error) {
    console.log(error);
    callBack(false, errorToText(error));
  }
};
