import axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Auth, FC_GetAllTermination } from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import TextArea from "../../components/Inputs/TextArea";
import TextInput from "../../components/Inputs/TextInput";
import SelectCustom from "../../components/SelectCustom/SelectCustom";
import { APP_URL } from "../../config/app.config";
import {
  APITerminationType,
  TerminationReasonType,
} from "../../types/StaffTermination";
import { BasicInfoType } from "../../types/userInfo";
import { setAxiosToken } from "../../utils/AxiosToken";
import { errorToText } from "../../utils/errors";
import { isEmptyOrSpaces } from "../../utils/functions";

type HTTerminationFormProps = {
  auth: Auth;
  selectedStaff: BasicInfoType;
  close: (value: any) => void;

  //   FROM REDUX ===========
  FC_GetAllTermination: Function;
};

type HTTerminationFormState = {
  loading: boolean;
  allReasons: TerminationReasonType[] | null;
  loadingRequest: boolean;
  staffRegId: string;
  staffCode: string;
  thisReason: TerminationReasonType | null;
  comment: string;
  error: {
    target:
      | "main-success"
      | "main-error"
      | null
      | "reason_id"
      | "comment"
      | "reg_id"
      | "staff_code";
    msg: string;
  };
  selectReason: boolean;
};

class HTTerminationForm extends Component<
  HTTerminationFormProps,
  HTTerminationFormState
> {
  constructor(props: HTTerminationFormProps) {
    super(props);

    this.state = {
      loading: true,
      allReasons: null,
      loadingRequest: false,
      thisReason: null,
      comment: "",
      error: {
        target: null,
        msg: "",
      },
      staffRegId: "",
      staffCode: "",
      selectReason: false,
    };
  }

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setSelectReason = (value: boolean) => {
    this.setState({ selectReason: value });
  };

  setLoadingRequest = (value: boolean) => {
    this.setState({ loadingRequest: value });
  };

  setAllReasons = (value: TerminationReasonType[] | null) => {
    this.setState({ allReasons: value });
  };

  setThisReason = (value: TerminationReasonType | null) => {
    this.setState({ thisReason: value });
  };

  setComment = (value: string) => {
    this.setState({ comment: value });
  };

  setStaffCode = (value: string) => {
    this.setState({ staffCode: value });
  };

  setStaffRegId = (value: string) => {
    this.setState({ staffRegId: value });
  };

  cleanToCLose = () => {
    this.props.close(null);
    this.setThisReason(null);
    this.setComment("");
    this.setStaffCode("");
    this.setStaffRegId("");
    this.setState({
      error: { target: null, msg: "" },
    });
  };

  getTerminationReasons = async (
    callBack: (
      status: boolean,
      data: TerminationReasonType[] | null,
      msg: string
    ) => void
  ) => {
    try {
      callBack(true, null, "");
      setAxiosToken();
      const res = await axios.get<TerminationReasonType[]>(
        `${APP_URL}/changeStatusReasons/termination`
      );

      console.log("====================================");
      console.log({ getTerminationReasons: res });
      console.log("====================================");

      callBack(false, res.data, "");
    } catch (error) {
      console.log(error);
      callBack(false, null, errorToText(error));
    }
  };

  componentDidMount = () => {
    if (this.props.auth.school === null) {
      this.setState({
        error: {
          target: "main-error",
          msg: "No school assigned to you",
        },
      });
    } else if (this.state.allReasons === null) {
      this.getTerminationReasons(
        (
          status: boolean,
          data: TerminationReasonType[] | null,
          msg: string
        ) => {
          this.setLoading(status);
          if (!status && isEmptyOrSpaces(msg) && data) this.setAllReasons(data);
          else if (!status && !isEmptyOrSpaces(msg))
            this.setState({
              error: {
                target: "main-error",
                msg: msg,
              },
            });
        }
      );
    }
  };

  submitTermination = async (e: any) => {
    e.preventDefault();
    // nid
    if (!this.props.auth.school)
      return this.setState({
        error: {
          target: "main-error",
          msg: "No school assigned to you!",
        },
      });

    if (!this.props.auth.user)
      return this.setState({
        error: {
          target: "main-error",
          msg: "Can't identify who you are!",
        },
      });

    if (
      isEmptyOrSpaces(this.props.auth.user.user_id) ||
      this.props.auth.user.user_id === undefined
    )
      return this.setState({
        error: {
          target: "main-error",
          msg: "Can't identify your code!",
        },
      });

    // reason_id
    if (this.state.thisReason === null)
      return this.setState({
        error: { target: "reason_id", msg: "Required!" },
      });

    // comment
    if (isEmptyOrSpaces(this.state.comment))
      return this.setState({
        error: { target: "comment", msg: "Required!" },
      });

    try {
      this.setLoadingRequest(true);
      setAxiosToken();
      const res = await axios.post<any>(
        `${APP_URL}/changeteacherstatus/requestchangeteacherstatus`,
        {
          changeStatusType: "PENDING TERMINATION",
          staff_to_changestatus_id: this.props.selectedStaff.staff_code,
          school_code: this.props.auth.school.school_code,
          district_code: this.props.auth.school.school_code.substring(0, 2),
          requested_by_reason_id: this.state.thisReason.termination_reason_id,
          requested_by_comment: this.state.comment,
        }
      );

      console.log("====================================");
      console.log({ submitTermination: res });
      console.log("====================================");

      this.props.FC_GetAllTermination(
        "hd",
        (status: boolean, data: APITerminationType[] | null, msg: string) => {
          this.setLoading(status);
          if (!status && !isEmptyOrSpaces(msg)) {
            console.log("====================================");
            console.error({ msg });
            console.log("====================================");
          } else {
            this.setState({
              loading: false,
              thisReason: null,
              comment: "",
              error: {
                target: "main-success",
                msg: "Staff  termination requested successfully",
              },
            });
            this.setLoadingRequest(false);

            setTimeout(() => {
              this.cleanToCLose();
            }, 2000);
          }
        }
      );
    } catch (error) {
      console.error(error);
      this.setState({
        loading: false,
        error: {
          target: "main-error",
          msg: errorToText(error),
        },
      });
    }
  };

  render() {
    return (
      <>
        {this.state.error.target === "main-success" && (
          <Alert theme="success">{this.state.error.msg}</Alert>
        )}
        {this.state.error.target === "main-error" && (
          <Alert theme="danger">{this.state.error.msg}</Alert>
        )}
        <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
          <div className="py-2 flex items-center">
            <div className="flex-1">
              <Heading>Staff termination form</Heading>
            </div>
            <div className="">
              <Button
                onClick={() => this.cleanToCLose()}
                theme="default"
                disabled={this.state.loadingRequest}
              >
                Close
              </Button>
            </div>
          </div>
          <form onSubmit={this.submitTermination}>
            <div className="p-2 flex flex-wrap">
              <div className="w-full py-1 px-3">
                <TextInput
                  disabled={true}
                  label="Full name"
                  name="full_name"
                  type="text"
                  value={
                    this.props.selectedStaff.full_name
                      ? this.props.selectedStaff.full_name
                      : ""
                  }
                  placeholder={"Full name"}
                  className="p-2"
                  onChange={() => null}
                  error={""}
                />
              </div>

              <div className="w-full py-1 px-3">
                <div className={`w-full my-2`}>
                  <label
                    htmlFor="login-form-email"
                    className={`text-primary-900 dark:text-primary-100`}
                  >
                    Termination reasons
                  </label>
                  {!this.state.selectReason ? (
                    <div className={`rounded relative`}>
                      <div
                        className={`w-full text-lg rounded border-2 p-2 px-4 text-md ${
                          !this.state.thisReason && "text-gray-500"
                        } ${
                          this.state.allReasons
                            ? "cursor-pointer"
                            : "cursor-wait"
                        } ${
                          this.state.error.target &&
                          this.state.error.target === "reason_id"
                            ? "border-red-500 dark:text-white bg-red-100 dark:bg-red-900 dark:bg-opacity-30"
                            : "border-primary-800 dark:text-white bg-white dark:bg-primary-900"
                        } px-3`}
                        onClick={() =>
                          !this.state.loading &&
                          !this.state.loadingRequest &&
                          this.setSelectReason(true)
                        }
                      >
                        {this.state.thisReason
                          ? this.state.thisReason.reason_name
                          : "Select termination reasons"}
                      </div>

                      {this.state.error.target &&
                        this.state.error.target === "reason_id" && (
                          <p className="text-sm text-red-600 dark:text-red-500">
                            {this.state.error.msg}
                          </p>
                        )}
                    </div>
                  ) : (
                    <SelectCustom
                      title="reason_name"
                      id="termination_reason_id"
                      data={this.state.allReasons}
                      loading={this.state.loading || this.state.loadingRequest}
                      click={(value: any) => {
                        this.setThisReason(
                          value as TerminationReasonType | null
                        );
                        this.setSelectReason(false);
                      }}
                      close={() => this.setSelectReason(false)}
                    />
                  )}
                </div>
              </div>

              <div className="w-full py-1 px-3">
                <TextArea
                  disabled={this.state.loading || this.state.loadingRequest}
                  label="Comment"
                  name="other_reason"
                  value={this.state.comment}
                  placeholder={"Other termination reason"}
                  className="p-2"
                  onChange={(e) => this.setComment(e.target.value)}
                  error={
                    this.state.error.target &&
                    this.state.error.target === "comment"
                      ? this.state.error.msg
                      : ""
                  }
                />
              </div>
            </div>
            <div className="px-6 text-right flex">
              <div className="flex-1">
                {this.state.error.target !== null &&
                  this.state.error.target !== "main-error" &&
                  this.state.error.target !== "main-success" && (
                    <div className="text-sm text-red-400 pr-4">
                      Some fields are required!
                    </div>
                  )}
              </div>
              <div className="">
                <Button type="submit" size="lg" loading={this.state.loading}>
                  Send
                </Button>
              </div>
            </div>
          </form>
        </DashboardContainer>
      </>
    );
  }
}

export default connect(null, { FC_GetAllTermination })(HTTerminationForm);
